@-webkit-keyframes xpand-in {
  100% {
    left: 50%;
    right: 50%; }
  0% {
    left: 0;
    right: 0; } }

@keyframes xpand-in {
  100% {
    left: 50%;
    right: 50%; }
  0% {
    left: 0;
    right: 0; } }

@-webkit-keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fade-in {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes xpand-out {
  0% {
    left: 50%;
    right: 50%; }
  100% {
    left: 0;
