div.a,
a {
  text-decoration: none;
  color: #ddc239; }
  div.a.link,
  a.link {
    position: relative;
    -webkit-transition: color 0.2s;
    transition: color 0.2s;
    white-space: nowrap; }
    div.a.link::after,
    a.link::after {
      content: '';
      position: absolute;
      bottom: -2px;
      left: 50%;
      right: 50%;
      border-bottom: 2px solid transparent;
      -webkit-animation: xpand-in 0.16s ease-in;
              animation: xpand-in 0.16s ease-in;
      -webkit-transition: opacity 1s;
      transition: opacity 1s;
      border-color: #6fcaa0;
      opacity: 0; }
    div.a.link:hover,
    a.link:hover {
      cursor: pointer; }
      div.a.link:hover::after,
      a.link:hover::after {
        left: 0;
        right: 0;
        bottom: -2px;
        -webkit-animation: xpand-out 0.16s;
                animation: xpand-out 0.16s;
        opacity: 1;
        -webkit-transition: opacity .01s;
        transition: opacity .01s; }
  div.a:hover,
  a:hover {
